.Home {
    margin-bottom: 2cm;
    display: flex;
    flex-direction: column;
    row-gap: 3cm;
}

.para-box {
    display: flex;
    margin-top: 1cm;
    flex-direction: column;

    row-gap: 2cm;
}

.para-one {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}

.tagline {
    font-size: 40px;
    line-height: 1.05cm;
    font-weight: bolder;
    font-family: play;
    color: rgb(31, 76, 211);
    margin-bottom: .2cm;
    letter-spacing: .035cm;
}

.check {
    width: .52cm;
}

.nismT {
    margin-top: .2cm;
}

.explore {
    margin-top: .5cm;
    padding: .35cm .8cm;
    font-size: 18px;
    border: 3px solid royalblue;
    background-color: royalblue;
    color: white;
    border-radius: 1000px;
    cursor: pointer;
    transition: .4s;

    &:hover {
        background-color: transparent;
        color: black;
    }
}

.heading-top {
    margin-bottom: .6cm;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.view-all {
    padding: .25cm .5cm;
    font-size: 15px;
    border: 3px solid rgb(230, 65, 87);
    background-color: rgb(230, 65, 87);
    color: white;
    border-radius: 1000px;
    cursor: pointer;
    transition: .4s;

    &:hover {
        background-color: transparent;
        color: black;
    }
}

.get-list-box {
    margin-top: .1cm;
    display: flex;
    flex-direction: column;
    justify-content: center;
    row-gap: .08cm;
}

.get-list {
    display: flex;
    align-items: center;
    column-gap: .08cm;
}

.report-date {
    color: rgb(107, 107, 107);
    // font-weight: 600;
}

.projects-display-heading {
    font-size: 35px;
    text-align: center;
    font-family: Dancing Script;
    color: black;
    font-weight: 600;

}

.desc {
    font-size: 20px;
    font-family: sarabun;
    color: rgb(72, 72, 72);
}

.sub-tagline {
    font-size: 21px;
    font-family: sarabun;
    color: rgb(72, 72, 72);
}

.left-one {
    width: 18cm;
}

.corss {
    width: 11cm;
}

.course {
    width: 8.5cm;
    height: auto;
    border: 1px solid rgb(201, 201, 201);
    background-color: whitesmoke;
    cursor: pointer;
    border-radius: 10px;
    padding: .3cm;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
}

.courses-container {
    display: flex;
    justify-content: center;
    column-gap: .7cm;
    flex-wrap: wrap;
    row-gap: .7cm;

}



.thumbnail {
    width: 100%;
    height: auto;
    border-radius: 10px;
}

.course-dets-section {

    display: flex;
    flex-direction: column;
    row-gap: .15cm;

}

.course-price-section {
    display: flex;
    align-items: baseline;
    justify-content: space-between;
}



.course-name {
    font-family: sarabun;
    font-size: 20px;
    font-weight: 600;
}

.course-price {
    font-family: roboto;
    font-size: 16px;
    font-weight: 600;
}

.course-owner {
    color: rgb(74, 74, 74);
    font-size: 14px;
    font-family: roboto;
}

.faq {
    width: 100%;
    border: 1px solid rgb(201, 201, 201);
    background-color: whitesmoke;
    padding: .4cm .5cm;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    row-gap: .2cm;
}

.downA {
    width: .4cm;
    height: .4cm;
    transition: .35s;
    opacity: .6;
}

.faq-ques {
    width: 100%;


    font-family: sarabun;
    font-weight: 600;
    font-size: 20px;

}

.faq-ans {
    font-size: 18px;
}

#faqAnsOne,
#faqAnsTwo,
#faqAnsThree,
#faqAnsFour {
    display: none;
}

.ques-sec {
    display: flex;
    // border: 1px solid black;
    cursor: pointer;
    justify-content: space-between;
}

.faq-container {
    flex-direction: column;
    display: flex;
    row-gap: .4cm;
}