// logo file
.logo {
    width: 4cm;
    height: auto;
    cursor: pointer;
}

// navigation bar container
.Nav {
    margin: .5cm 0cm;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

// navigation menu
.menu {
    display: flex;
    align-items: center;
    column-gap: .5cm;
}

// design for selected menu item
.menu-item-nav {
    text-decoration: underline;
    text-decoration-color: rgb(0, 0, 0);
    text-decoration-thickness: 2px;
    text-underline-position: under;
}